* {
  box-sizing: border-box;
}
body {
  font-family: "Londrina Solid";
  background-image: url(./img/fondo.png);
  margin: 0;
  padding: 0 34px;
}
.container-home,
.container-game,
.container-instructions,
.container-result-win,
.container-result-lose {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
  max-width: 370px;
}
@media (min-width: 960px) {
  .container-home,
  .container-game,
  .container-instructions,
  .container-result-win,
  .container-result-lose {
    max-width: 550px;
  }
}

.home__container-title {
  font-size: 80px;
  margin-bottom: 35px;
}
@media (min-width: 960px) {
  .home__container-title {
    font-size: 100px;
    margin-bottom: 100px;
  }
}
.home__title {
  text-align: center;
}
.home__container-button {
  width: 100%;
  margin: 0 auto;
}
@media (min-width: 960px) {
  .home__container-button {
    width: 450px;
  }
}
.home__container-plays {
  margin: 0 20px;
  margin-top: 60px;
  display: flex;
  justify-content: space-between;
}
@media (min-width: 960px) {
  .home__container-plays {
    margin-top: 120px;
  }
}
.home__container-play-piedra,
.home__container-play-papel,
.home__container-play-tijera {
  width: 56px;
  height: 128px;
}
@media (min-width: 960px) {
  .home__container-play-piedra,
  .home__container-play-papel,
  .home__container-play-tijera {
    width: 100px;
    height: 180px;
  }
}
.inst__container-title {
  font-size: 40px;
  max-width: 310px;
  place-self: center;
  margin-bottom: 80px;
}
@media (min-width: 960px) {
  .inst__container-title {
    max-width: 410px;
    font-size: 50px;
    margin-bottom: 70px;
  }
}
.instructions__text {
  text-align: center;
}
.instructions__container-button {
  width: 100%;
  margin: 0 auto;
}
@media (min-width: 960px) {
  .instructions__container-button {
    width: 450px;
  }
}
.game__container-count {
  place-self: center;
  height: 335px;
  width: 307px;
  text-align: center;
  font-size: 300px;
}
.game__container-plays {
  margin: 0;
  margin-top: 60px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.game__container-play-piedra,
.game__container-play-papel,
.game__container-play-tijera {
  opacity: 80%;
  width: 103px;
  height: 225px;
}
@media (min-width: 960px) {
  .game__container-play-piedra,
  .game__container-play-papel,
  .game__container-play-tijera {
    margin-top: 50px;
    width: 133px;
    height: 255px;
  }
}
.game__container-play-piedra:hover,
.game__container-play-papel:hover,
.game__container-play-tijera:hover {
  opacity: 100%;
  flex-grow: 4;
  height: 260px;
  width: 103px;
}

.game__time-over-text {
  font-size: 80px;
  text-align: center;
}
@media (min-width: 960px) {
  .game__time-over-text {
    font-size: 100px;
    text-align: center;
  }
}
.game__container-buttons {
  margin-top: 35px;
  display: grid;
  gap: 20px;
}
@media (min-width: 960px) {
  .game__container-buttons {
    margin: 0 auto;
    margin-top: 130px;
    width: 450px;
  }
}
.game__container-result {
  display: grid;
  gap: 40px;
  justify-items: center;
}
.game__container-result-play {
  height: 300px;
  width: 169px;
}
@media (min-width: 960px) {
  .game__container-result-play {
    height: 370px;
    width: 200px;
  }
}
.result__container-result {
  display: flex;
  justify-content: center;
  align-items: center;
}
.result__container-result,
.result__container-score {
  font-size: 40px;
  margin-bottom: 40px;
  place-self: center;
}
@media (min-width: 960px) {
  .result__container-result,
  .result__container-score {
    margin-bottom: 55px;
    font-size: 58px;
    width: 300px;
    height: 300px;
  }
}

.result-win__background,
.result-lose__background {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
}
.result-win__background {
  background-color: #888949;
  opacity: 80%;
}
.result-lose__background {
  background-color: #894949;
  opacity: 80%;
}
.result__container-button {
  width: 100%;
  margin: 0 auto;
}
@media (min-width: 960px) {
  .result__container-button {
    width: 450px;
  }
}
